<template>
  <div class="loginContainer">
      <heade-top :head-title="loginWay? '登陆': '密码登陆'" goBack="true"></heade-top>
      <form class="loginForm" v-if="loginWay">
          <section class="input_container phone_number">
              <input type="text" placeholder="账号密码随便输" name="phone" maxlength="11" v-model="phoneNumber">
              <!-- @click.prevent点击时阻止默认事件 -->
              <button @click.prevent="getVerifyCode" :class="{right_phone_number:rightPhoneNumber}" v-show="!computedTime" >获取验证码</button>
              <button @click.prevent v-show="computedTime">已发送({{computedTime}})</button>
          </section>
      </form>
     <form v-else class="loginForm">
        <section class="input_container">
            <!-- v-model.lazy失去焦点时才触发 -->
            <input type="text" placeholder="请输入账号" v-model.lazy="userAccount">
        </section>
        <section class="input_container">
            <input v-if="!showPassword" type="password" placeholder="请输入密码" v-model="passWord">
            <input v-else type="text" placeholder="请输入密码" v-model="passWord">
            <div class="button_switch" :class="{change_to_text:showPassword}" >
                <div class="circle_button" @click="changePassWordType" :class="{trans_to_right:showPassword}">
                </div>
                <span>abc</span>
                <span>...</span>
            </div>
        </section>
        <section class="input_container captcha_code_container">
            <input type="text" placeholder="验证码" maxlength="4" v-model="CodeNumber"/>
            <div class="img_change_img">
                  <img v-show="captchaCodeImg" :src="captchaCodeImg">
                <div class="change_img" @click="getCaptchaCode">
                    <p>看不清</p>
                    <p>换一张</p>
                </div>
            </div>
        </section>
     </form>
     <p class="login_tips">
         温馨提示未注册的号码登陆时自动注册
     </p>
    <p class="login_tips">
        注册过的用户可凭账号密码登陆
    </p>
    <div class="login_container" @click="mobileLogin">登陆</div>
    <router-link to="/forget" v-if="loginWay" class="to_forget">重置密码?</router-link>
    <alert-tip v-if="showAlert" :showHide="showAlert" @closeTip="closeTip" :alertText="alertText"></alert-tip>
  </div>
</template>

<script>
import headeTop from '../../components/header/header'
import {getcaptchas,accountLogin} from '../../service/getData'
import alertTip from '../../components/common/alertTip'
import {mapState,mapMutations} from 'vuex'
export default {
    data(){
        return{
            loginWay:false,//登陆方式默认短信登陆
            showPassword:false,//是否显示密码
            phoneNumber:null,//电话号码
            mobileCode:null,//短信验证码
            validate_token:null,//获取短信是的验证值,登陆时需要
            computedTime:null,//登陆倒计时
            userInfo:null,//获取到的用户信息
            userAccount:null,//用户名
            passWord:null,//密码
            captchaCodeImg:null,//验证码地址
            CodeNumber:null,//验证码
            showAlert:null,//显示提示组建
            alertText:null//显示提示的文章
        }
    },
    components: {
      headeTop,
      alertTip
    },
    computed: {
        ...mapState([

        ])
    },
    created () {
        this.getCaptchaCode();
    },
    methods:{
        ...mapMutations([
            'RECORD_USERINFO'
        ]),
       async getCaptchaCode(){
           let res=await getcaptchas();
           
           this.captchaCodeImg=res.code;
        },
        //是否显示密码
        changePassWordType()
        {
            this.showPassword=!this.showPassword;
        },
        //登陆
       async mobileLogin()
        {
            if(!this.userAccount)
            {
                this.alertText="请输入账号";
                this.showAlert=true;
                return;
            }else if(!this.passWord)
            {
                this.alertText="请输入密码";
                this.showAlert=true;
                return;
            }else if(!this.CodeNumber)
            {
                this.alertText="请输入验证码";
                this.showAlert=true;
                return;
            }
            //用户名登陆 
            alert(this.CodeNumber)
            this.userInfo=await accountLogin(this.userAccount,this.passWord,this.CodeNumber);
            //看返回值正不正确
            if(!this.userInfo.user_id)
            {
                this.alertText=this.userInfo.message;
                this.showAlert=true;
                if(!this.loginWay) this.changePassWordType();
            }else{
            
                this.RECORD_USERINFO(this.userInfo);
                this.$router.go(-1);
            }
        },
        //关闭弹出框
        closeTip()
        {
            this.showAlert=!this.showAlert;
        }
    }
}
</script>

<style scoped lang="scss">
@import '../../style/mixin.scss';
.loginContainer{
    padding-top: 1.95rem;
    p, span , input{
        font-family: Helvetica Neue,Tahoma,Arial;
    }
}
.loginForm{
    background-color: #fff;
    margin-top: 0.6rem;
    .input_container{
        display: flex;
        justify-content: space-between;//弹性项目平均分布在该行上
        padding: .6rem .8rem;
        border-bottom: 1px solid #f1f1f1;
        input{
            @include sc(.7rem,#666);

        }
        button{
            @include sc(.65rem,#fff);
            font-family: Helvetica Neue,Tahoma,Arial;
            padding: .28rem .4rem;
            border:1px;
            border-radius: .15rem;
        }
        .right_phone_number{
            background-color: #4cd964;
        }
    }
    .phone_number{
        padding: .3rem .8rem;
    }    
    .captcha_code_container{
        height: 2.2rem;
        .img_change_img{
            display: flex;
            align-items: center;
            img{
                @include wh(3.5rem,1.5rem);
                margin-right: .2rem;
            }
            .change_img{
                display: flex;
                flex-wrap: wrap;
                width: 2rem;
                justify-content: center;
                p{
                    @include sc(.55rem,#666);

                }
                p:nth-of-type(2)
                {
                    margin-top: .2rem;
                    color:#3b96e5;
                }

            }
        }
    }
}
.login_tips{
    @include sc(.5rem,red);
    padding: .4rem .6rem;
    line-height: .5rem;
    a{
        color:#3b96e5;
    }
    
}
.login_container{
    margin:0.5rem 1rem;
    border:1px;
    @include sc(.7rem,#fff);
    background-color: #4cd964;
    border-radius: 0.5rem;
    text-align: center;
}
.button_switch{
    background-color:#ccc;
    display: flex;
    justify-content: center;
    @include wh(2rem,.7rem);
    padding:0.2rem;
    border:1px;
    border-radius: 0.5rem;
    position: relative;
    .circle_button{
        transition: all .3s;
        position: absolute;
        top: -0.2rem;
        z-index: 1;
        left:-0.3rem;
        @include wh(1.2rem,1.2rem);
        box-shadow: 0 0.026667rem 0.053333rem 0 rgba(0,0,0,.1);//属性向框添加一个或多个阴影
        background-color: #f1f1f1;
        border-radius: 50%;

    }
    .trans_to_right{
        transform: translateX(1.3rem);
    }
    span{
        @include sc(.45rem,#fff);
        transform: translateY(.05rem);
        line-height: .6rem;

    }
    span:nth-of-type(2){
        transform: translateY(-.08rem);
    }
}
.change_to_text{
    background-color: #3b96e5;
}
.to_forget{
    float:right;
    @include sc(.6rem,#3b95e9);
    margin-right: .3rem;
}

</style>



